import React, {useState, useEffect} from 'react';
import './SectionCard.css';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "./Card/Card";
import SubmitButton from './SubmitButton/SubmitButton';

export default function SectionCard() {
    const [num, setNumState] = useState("");

    const [hasError, setErrors] = useState(false);
   const [planets, setPlanets] = useState({});


  

    const AddNumber = (val) => {
        const addmoney = num;
        
        if((addmoney.toString().length+1) <= 16){ //9 billones
            if(addmoney == 0){
                setNumState(val.target.value);
            } else {
                setNumState( addmoney + val.target.value);
            }
        }
      };

      async function doNumber() {    
        
        const dateOrder = {
          cardNumbers: num,
        };
        try {
          let res = await fetch('/isValidateNumber', {
            method: 'post',
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            body: JSON.stringify({
              numberCard: dateOrder.cardNumbers
            }
            )
          }).then( (response) =>{
            console.log(response);
          })
          let result = await res.json();
           if (result && result.success){
             console.log("funca locooo");
           }

           else if (result && result.success === false){
             console.log("No funca!!!!");
             console.log("probando " + result.msg);
           }
        }
        catch(e){
          console.log(e);
        }

        console.log(dateOrder);
        

      }

      const handleDelete = (val) => {
        if(num != 0)
        setNumState( num.substring(0,num.length-1));
      };

      const setInputValue = (property, val) => {
        val = val.trim();
        if (val.length > 16){
          return;
        }
      };

      

    return (
      <Row className="mt-2">
        <Col sm="4">
          <Card 
              var1 = {num}
              setInputValue = {setInputValue}
              doNumber={doNumber}
          />
        </Col>
        <Col sm="8">
        <div className="calcShow">
                    <div className="numbers">
                        <div className="numbersAnimation">
                            <button    onClick={AddNumber} value="1" className="number">1</button>
                            <button   onClick={AddNumber} value="2" className="number">2</button>
                            <button   onClick={AddNumber} value="3" className="number">3</button>    
                        </div>
                        <div className="numbersAnimation">
                            <button   onClick={AddNumber} value="4" className="number">4</button>
                            <button   onClick={AddNumber} value="5" className="number">5</button>
                            <button   onClick={AddNumber} value="6" className="number">6</button>
                        </div>
                        <div className="numbersAnimation">
                            <button   onClick={AddNumber} value="7" className="number">7</button>
                            <button   onClick={AddNumber} value="8" className="number">8</button>
                            <button   onClick={AddNumber} value="9" className="number">9</button>
                        </div>
                        <div className="numbersAnimation">
                            <button   onClick={handleDelete} value="delete" className="number">&lt;</button>
                            <button   onClick={AddNumber} value="0" className="number">0</button>
                             <button  onClick={AddNumber} className="number ok">Ing</button>
                        </div>
                    </div>             
                </div>
        </Col>
        <Col sm="12">
        <SubmitButton  Text="Ingresar al sistema"
           doNumber={doNumber}/>
        </Col>
      </Row>
    )
}
